body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
}
.zoo {
    position: relative;
}
.zoo-content {
    position: relative;
}
.zoo-visor {
    position: absolute;
    width: 500px;
    height: 500px;
    background: transparent;
    cursor: crosshair;
    z-index: 10; /* Esta por encima de todo */
 }

.zoo-lens {
    position: absolute;
    border: 1px solid #d4d4d4;
    background-color: rgba(255, 255, 255, .4);
    width: 260px;
    height: 260px;
    z-index: 1; /* Entre imagen y visor*/
}

.zoo-result {
    border: 1px solid #d4d4d4;
    width: 520px;
    height: 520px;
    position: absolute;
    top: 0;
    left: 505px;
    z-index: 1000;
}

.content {
  width: 1200px;
  margin: 0 auto;
}
.content h1 {
  text-align: center;
}

